@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: 'Montserrat', cursive;
}


.panel {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding: 36px 24px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin-left: 20px;
    margin-right: 20px;
    transition: all 0.4s ease-in;
    border-radius: 4px;
    align-items: center;
}

.panel-dark {
    /* box-shadow: none; */
    background-color: #33333D;
}

.panel-form {
    flex: 0 1;
    /* max-w */
}
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1040;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    opacity: .5;
  }
  
  .modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
  }
  
  .modal {
    z-index: 100;
    background: white;
    position: relative;
    margin: 1.75rem auto;
    border-radius: 3px;
    max-width: 500px;
    padding: 2rem;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    top: 20%;
  }
  
  .modal-header {
    display: flex;
    justify-content: flex-end;
  }
  
  .modal-close-button {
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    opacity: .3;
    cursor: pointer;
    border: none;
  }

.form{
    display: flex;
    flex-direction: column;
    width: 20vw;
    /* align-items: center; */
}

.fileContainer{
    box-shadow: none;
    border: dashed;
    padding: 10px;
    margin: 0px;
}

.fileContainer > p {
    display: none;
}

.fileContainer .chooseFileButton{
    border-radius: 4px;
}

.image-uploader {
    padding-bottom: 40px;
}
.radio-group {
    padding: 10px;
    padding-bottom: 40px;
}


.image {
    width: 400px;
    max-width: 40vw;
}
